import React, { Component } from "react";
import "./App.css";
import ChatSecion from "./components/ChatSecion";
import ChatUserInputSec from "./components/ChatUserInputSec";
import MainContainer from "./components/MainContainer";
import SideBar from "./components/SideBar";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chatItems: [
        // Initial chat items
        {
          name: "Assistant",
          model: "gpt-3.5-turbp",
          comment: "this is a comment",
        },
        { name: "Ahmad", comment: "this is a reply" },
        { name: "System", comment: "Meh" },
        // ... other initial chat items ...
      ],
    };
  }

  // Function to add a new chat item
  addChatItem = (newChatItem) => {
    this.setState((prevState) => ({
      chatItems: [...prevState.chatItems, newChatItem],
    }));
  };
  render() {
    return (
      <div>
        <MainContainer>
          <SideBar />
          <ChatSecion chatItems={this.state.chatItems} />
          <ChatUserInputSec />
        </MainContainer>

        {/* <button>Ahmad</button>
      <button>Send</button>
      <button>Send</button>
      <button>Send</button> */
        /* <MainContainer className="MainContainer">
        <ChatSecion>
          <ChatList>
            <ChatItem />
            <ChatItem />
            <ChatItem />
          </ChatList>
          <ChatUserInputSec>
            <input>ChatTextParagraphField </input>
            <input type="radio">ChooseGptVersionRadio</input>
            <button>Send</button>
          </ChatUserInputSec>
        </ChatSecion>
        <SideBar>
          <button>StartNewchatBtn</button>
          <HistorySection>
            <HistoryItem />
            <HistoryItem />
            <HistoryItem />
            <HistoryItem />
          </HistorySection>
        </SideBar>
      </MainContainer> */}
      </div>
    );
  }
}

export default App;
